.clients {
  height: 20vh;
  width: 100%;
  margin: 0 auto 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  img {
    height: 65px;
    width: auto;

    @media (max-width: 1600px) {
      height: auto;
    }
